<template>
  <section class="bmt__area">

    <div class="center bmt__menu">
      <a
        class="btn btn--diamond"
        v-for="(val, key) in componentAlphabet"
        :key="key"
        @click="updateView(componentList[val], val)"
        :class="{'btn--active':val === active}"
      >
        <span>{{val}}</span>
      </a>

      <a
        class="btn btn--diamond"
        @click="copyToClipboard()"
        v-if="isUploader"
      >
        <span>edit</span>
      </a>
      
      <a
        class="btn btn--diamond"
        @click="deleteRecord()"
        v-if="isUploader"
      >
        <span>delete</span>
      </a>

      <a
        class="btn btn--diamond"
        @click="copyToClipboard()"
        v-if="!isUploader"
      >
        <span>Export</span>
      </a>
      
      
    </div>

    <keep-alive>
      <component
        class="bmt__big"
        :is='component'
        :data='parsed'
      ></component>
    </keep-alive>

    <transition name='bounce'>
      <Modal
        v-if="pokemonExportModal"
        @close-my-modal="pokemonExportModal = false"
        :sizeClass='"modal__content--large"'
      >
        <textarea
          class="modal__export"
          v-model="parsed.raw"
        ></textarea>
        <button @click="updateData" v-if="isUploader">save changes</button>
      </Modal>
    </transition>

  </section>
</template>

<script>
import axios from 'axios';
import Modal from "@/components/Modal";
import TeamViewGeneral from "@/components/TeamView/_TeamView.vue";
import TeamViewCounter from "@/components/TeamView/_TeamViewCounter.vue";

export default {
  components: {
    TeamViewCounter,
    TeamViewGeneral,
    Modal
  },
  data() {
    return {
      component: "TeamViewGeneral",
      active: "Viewer",
      componentList: {
        Viewer: "TeamViewGeneral",
        Calcs: "TeamViewCounter"
      },
      componentAlphabet: ["Viewer", "Calcs"],
      pokemonExportModal: false,
      isUploader: false,
    };
  },
  props: ["parsed"],
  methods: {
    updateView(component, active) {
      this.component = component;
      this.active = active;
    },
    copyToClipboard() {
      this.pokemonExportModal = true;
    },
    updateData(){
      const data = {
        raw: this.parsed.raw,
        teamMembers: this.parsed.pokemon.map(p=>{
          return {name: p.data.slug}
        }),
        _id : this.$route.params.id 
      }
      axios.post('/mysquad/update', data)
        .then(res=>{
          if(res.data.code){
            this.$router.go();
          }
        })
        .catch(err=>{
          alert(err)
        })
    },
    deleteRecord(){
      axios.post("/mysquad/delete", {_id : this.$route.params.id})
        .then(()=>{
          let localStore = JSON.parse(localStorage.getItem('saved'))
          const index = localStore.indexOf(this.$route.params.id)

          if(index > -1){
            localStore.splice(index, 1)
            localStorage.setItem('saved', JSON.stringify(localStore))
          }

          this.$router.push({name: "mysquad"})
        })
        .catch(err=> alert(err))
    }
  },
  created(){
    const saved = JSON.parse(localStorage.getItem('saved'))
    const teamId = this.$route.params.id
    if(saved){
      if(saved.includes(teamId)) this.isUploader = true
    }
  }
};
</script>